export function getDefaultTokenList() {
  const env = getConfig().networkId;

  switch (env) {
    case 'mainnet':
      return DefaultMainnetTokenList;
    case 'testnet':
      return DefaultTestnetTokenList;
    default:
      return DefaultMainnetTokenList;
  }
}
